import React, { Component } from "react";
import Typical from "react-typical";
import Switch from "react-switch";
import { Icon } from '@iconify/react';
import roundLaptopChromebook from '@iconify/icons-ic/round-laptop-chromebook';

var name = 'Usman Khan';

class Header extends Component {
  titles = ['Mechanical Engineer', 'Robotics Engineer (MSc)', 'Front-End Developer'];
  

  constructor() {
    super();
    this.state = { checked: false };
    this.onThemeSwitchChange = this.onThemeSwitchChange.bind(this);
  }

  onThemeSwitchChange(checked) {
    this.setState({ checked });
    this.setTheme();
  }

  setTheme() {
    var dataThemeAttribute = "data-theme";
    var body = document.body;
    var newTheme =
      body.getAttribute(dataThemeAttribute) === "dark" ? "light" : "dark";
    body.setAttribute(dataThemeAttribute, newTheme);
  }

  render() {
    if (this.props.sharedData) {
      var names = 'Usman Khan';
      this.titles = this.props.sharedData.titles.map(x => [ x.toUpperCase(), 5000 ] ).flat();
    }

    const HeaderTitleTypeAnimation = React.memo( () => {
      return <Typical className="title-styles" steps={['Mechanical Engineer', 1200, 'Robotics Engineer (MSc)', 1200, 'Front-End Developer', 1200]} loop={10000} />
    }, (props, prevProp) => true);

    return (
      <header id="home" style={{ height: window.innerHeight - 140, display: 'block' }}>
        <div className="row aligner" style={{height: '100%'}}>
          <div className="col-md-12">
            <div>
              <Icon
                    // style = {{fontSize:'10rem'}}
                    icon={roundLaptopChromebook}
                    style={{ fontSize: "25rem", paddingBottom: '10rem' }}
              />
              <span className="iconify header-icon" data-icon="la:laptop-code" data-inline="false"></span>
              <br/>
              <h1 className="mb-0">
                <Typical steps={[name]} wrapper="p" />
              </h1>
              <h2 className="title-styles ">
                <Typical steps={['Mechanical Engineer', 1200]}/>
              </h2>
              <h2 className="title-styles ">
                <Typical steps={['Robotics Engineer (MSc)', 1200]}/>
              </h2>
              <h2 className="title-styles ">
                <Typical steps={['Front-End Developer', 1200]}/>
              </h2>
              <Switch
                checked={this.state.checked}
                onChange={this.onThemeSwitchChange}
                offColor="#baaa80"
                onColor="#353535"
                className="react-switch mx-auto"
                width={90}
                height={40}
                uncheckedIcon={
                  <span
                    className="iconify"
                    data-icon="twemoji:owl"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "20px",
                      color: "#353239",
                    }}
                  ></span>
                }
                checkedIcon={
                  <span
                    className="iconify"
                    data-icon="noto-v1:sun-with-face"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "10px",
                      color: "#353239",
                    }}
                  ></span>
                }
                id="icon-switch"
              />
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
